import Scroll from '../../../base/scroll/scroll'
import Pay from '../../../base/pay/pay'
import {mapGetters} from 'vuex'
export default {
  name: "buyCoin",
  components: {
    Scroll,
    Pay
  },
  data() {
    return {
      paytype: 2,
      isChecked: 0,
      title: '',
      moneyBox: [
        {
          price: '50',
          oldPrice: '50'
        },
        {
          price: '100',
          oldPrice: '10',
          info: '5'
        },
        {
          price: '200',
          oldPrice: '200',
          info: '16'
        },
        {
          price: '300',
          oldPrice: '300',
          info: '30'
        },
        {
          price: '500',
          oldPrice: '500',
          info: '75'
        },
      ],
      moneyIndex: 0,
      type: 1,
      chosed: true
    }
  },
  computed:{
    ataloCount:{
      get(){
        let ataloCount = 0
        let choosePrice = parseFloat(this.moneyBox[this.moneyIndex].price)
        ataloCount = choosePrice - (this.chosed?this.hasmoney:0)
        return ataloCount.toFixed(2)
      },
      set(val){
        return val
      }
    },
    hasmoney:{
      get(){
        let has = 0
        if(this.userCoin.availableDeposit){
          has = parseFloat(this.userCoin.availableDeposit).toFixed(2)
        }
        return has
      },
      set(val){
        return val
      }
    },
    ...mapGetters([
      'userInfo',
      'userCoin'
    ])
  },
  methods: {
    btn(){
        alert('')
    },
    choosePay(index) {
      this.moneyIndex = index
    },
    isChosed(val) {
      this.chosed = val
    }
  }
}



